import React, { useEffect, useState } from "react";
import BackBtn from "../../Components/UI/BackBtn/BackBtn";
import { checkSocialLogin } from "../../api/Business";
import classes from "./AccountSuccess.module.css";
import linkImg from "../../assets/icons/link.svg";
import facebook from "../../assets/icons-2/facebook (1).jpg";
import instagram from "../../assets/icons-2/instagram (2).jpg";
import linkedin from "../../assets/icons-2/linkedin (2).jpg";
import twitter from "../../assets/icons-2/twitter (1).jpg";
import styled, { css } from "styled-components";
import CustomButton from "../../Components/UI/Button/CustomButton";

const Img = styled.img`
  mix-blend-mode: unset;
  opacity: 1;

  ${(props) =>
    props.linked &&
    css`
      mix-blend-mode: luminosity;
      opacity: 0.5;
    `}
`;

const AccountSuccess = (props) => {
  const [data, setData] = useState(null);
  const [social, setSocial] = useState(null);

  useEffect(() => {
    checkSocialLogin(props.match.params.businessId).then((data) => {
      if (!data.error) {
        setData(data.data);

        setSocial(Object.assign({}, ...data.data.social_login));
      }
    });
  }, []);

  return (
    <section className={classes.Account_Success}>
      <div className={classes.Title}>
        <BackBtn />
        <div>
          <h1>
            You have successfully linked your social accounts with easysocial
          </h1>
        </div>
      </div>
      {data && (
        <div className={classes.Profile_Status}>
          <img src={data.photo} alt="Profile" />
          <p>
            {data.name} <br /> - {data.city}
          </p>
          <div className={classes.Icons}>
            <img className={classes.Icon_Link} src={linkImg} alt="link" />
            <div className={classes.Icons_Grid}>
              <Img
                linked={social && !social.facebook}
                className={classes.Icon}
                src={facebook}
                alt="Facebook"
              />
              <Img
                linked={social && !social.instagram}
                className={classes.Icon}
                src={instagram}
                alt="Instagram"
              />
              <Img
                linked={social && !social.twitter}
                className={classes.Icon}
                src={twitter}
                alt="Twitter"
              />
              <Img
                linked={social && !social.linkedIn}
                className={classes.Icon}
                src={linkedin}
                alt="LinkedIn"
              />
            </div>
          </div>
        </div>
      )}

      <CustomButton
        svgStyle={{ paddingLeft: "4%" }}
        className={classes.Btn}
        onClick={() => {
          props.history.push("/dashboard");
        }}
      >
        Begin with your first social post
      </CustomButton>
    </section>
  );
};

export default AccountSuccess;
